import React, { useState} from 'react'
import Select from 'react-select'
import { Link } from 'react-router-dom'
import {useDispatch,useSelector} from 'react-redux'
import axios from 'axios'
import { updateError, updateStart, updateSuccess } from '../redux/userSlice'
import { Navigate } from 'react-router'
import Loader from "react-loader-spinner";
import { useFormik } from 'formik'
import * as Yup from 'yup';





const Register = () => {
    const optionsIndustry = [
        { value: 'Advertising & Marketing', label: 'Advertising & Marketing' },
        { value: 'Agriculture, forestry and fishing', label: 'Agriculture, forestry and fishing' },
        { value: 'Biotechnology & Pharmaceuticals', label: 'Biotechnology & Pharmaceuticals' },
        { value: 'Construction, Infrastructure & Real Estate', label: 'Construction, Infrastructure & Real Estate' },
        { value: 'Education & Training', label: 'Education & Training' },
        { value: 'Electronics', label: 'Electronics' },
        { value: 'Financial Services & Insurance', label: 'Financial Services & Insurance' },
        { value: 'Health Care', label: 'Health Care' },
        { value: 'Industrial Services', label: 'Industrial Services' },
        { value: 'Information Technology', label: 'Information Technology' },
        { value: 'Manufacturing & Production', label: 'Manufacturing & Production' },
        { value: 'Mining and Quarrying', label: 'Mining and Quarrying' },
        { value: 'Non-profit and Charity Organisations', label: 'Non-profit and Charity Organisations' },
        { value: 'Retail', label: 'Retail' },
        { value: 'Social Services and Government Agencies', label: 'Social Services and Government Agencies' },
        { value: 'Telecommunications', label: 'Telecommunications' },
        { value: 'Trading', label: 'Trading' },
        { value: 'Transportation', label: 'Transportation' },
      ]

      const optionsDepartment = [
        { value: 'Account', label: 'Account' },
        { value: 'Administration', label: 'Administration' },
        { value: 'Designing', label: 'Designing' },
        { value: 'Dispatch', label: 'Dispatch' },
        { value: 'Finance', label: 'Finance' },
        { value: 'Human Resource', label: 'Human Resource' },
        { value: 'Information Technology', label: 'Information Technology' },
        { value: 'Inspection', label: 'Inspection' },
        { value: 'Maintenance', label: 'Maintenance' },
        { value: 'Marketing & Proposals', label: 'Marketing & Proposals' },
        { value: 'Packaging', label: 'Packaging' },
        { value: 'Procurement', label: 'Procurement' },
        { value: 'Production', label: 'Production' },
        { value: 'Project', label: 'Project' },
        { value: 'Research & Development', label: 'Research & Development' },
        { value: 'Sales', label: 'Sales' },
        { value: 'Security', label: 'Security' },
        { value: 'Store', label: 'Store' },
        { value: 'Management', label: 'Management' },
        { value: 'Business Owner', label: 'Business Owner' },
        { value: 'Business Head', label: 'Business Head' },
      ]

      const [count, setcount] = useState(1)
      const [success, setsuccess] = useState()
      const [error, setError] = useState()
      const dispatch = useDispatch();
      const phoneRegex = /^[6-9]\d{9}$/


      const {handleSubmit,handleChange,values,touched,errors,handleBlur,isValid} = useFormik({
        initialValues:{
            firstname:'',
            lastname:'',
            email:'',
            password:'',
            industry:'',
            department:'',
            phone:''
        },
        validationSchema:Yup.object({
            firstname:Yup.string().min(4,'Firstname should be atleast 4 characters').required('Required'),
            lastname:Yup.string().min(4,'Lastname must be atleast 4 characters').required('Required'),
            email:Yup.string().email('Please enter a valid email').required('Required'),
            password:Yup.string().min(8,'Password must be atlease 8 characters').required('Required'),
            phone: Yup.string().matches(phoneRegex, 'Phone number is not valid')
        }),
        onSubmit:async (values) => {
            console.log('clicked')
                dispatch(updateStart())
                await axios({
                    method:'post',
                    url:'/api/v1/register',
                    data:values,
                    headers:{"Content-Type":"application/json"},
                }).then((response) => {
                    localStorage.setItem('token',response.data.token)
                    const updatedUser = response.data.user
                    delete updatedUser["password"]
                    localStorage.setItem('user',JSON.stringify(updatedUser))
                    dispatch(updateSuccess(updatedUser))
                    setsuccess('Logged in successfully')
                }).catch((err) => {
                    setError(err.response.data.error)
                    dispatch(updateError())
                });
        }
    })

      const token = localStorage.getItem('token')
      const isLoggedIn = useSelector(state => state.user.isLoggedIn)
      const isLoading = useSelector(state => state.user.pending)

      if(isLoggedIn || token){
          return <Navigate to="/dashboard"/>
      }

    return (
        <div>
            <div className="flex flex-col justify-center items-center w-full md:w-96 px-10 py-8 dark:bg-coolGray-900 dark:text-coolGray-100 bg-white shadow-lg rounded-lg h-full">
                <div className="mb-4 text-center">
                    <h1 className="my-3 text-2xl md:text-2xl font-bold text-primary">Register</h1>
                    <h1 className='text-lg font-bold'>Step{count} of 2</h1>
                </div>
                <form noValidate="" action="" className="space-y-4 md:space-y-4 ng-untouched ng-pristine ng-valid">
                    <div className="space-y-1 md:space-y-2">
                        {count === 1 ? (
                            <div className="">
                               <div className="flex justify-between">
                               <label htmlFor="firstname" className="block mb-2 text-xs md:text-sm">Firstname</label>
                               </div>
                               <div className="flex justify-between flex-col">
                               <input type="firstname" 
                               name="firstname" 
                               id="firstname" 
                               placeholder="John" 
                               onChange={handleChange}
                               value={values.firstname}
                               onBlur={handleBlur}
                               className="w-full px-2 py-1 md:px-3 md:py-2 border rounded-md dark:border-coolGray-700 dark:bg-coolGray-900 dark:text-coolGray-100 bg-background focus:ring-primary mr-2"
                               data-test="firstname"
                               />
                               {touched.firstname && errors.firstname ? (
                                <div className='text-red-500 text-sm'>{errors.firstname}</div>
                                ): null}
                               <label htmlFor="lastname" className="block text-xs md:text-sm mt-2">Lastname</label>
                               <input type="lastname" 
                               name="lastname" 
                               id="lastname" 
                               placeholder="Doe" 
                               className="w-full px-2 py-1 md:px-3 md:py-2 border rounded-md dark:border-coolGray-700 dark:bg-coolGray-900 dark:text-coolGray-100 bg-background focus:ring-primary" 
                               onChange={handleChange}
                               value={values.lastname}
                               onBlur={handleBlur}
                               data-test="lastname"
                               />
                               {touched.lastname && errors.lastname ? (
                                <div className='text-red-500 text-sm'>{errors.lastname}</div>
                                ): null}
                                <label htmlFor="email" className="block text-xs md:text-sm mt-2">Email</label>
                               <input type="email" 
                               name="email" 
                               id="email" 
                               placeholder="john@doe.com" 
                               className="w-full px-2 py-1 md:px-3 md:py-2 border rounded-md dark:border-coolGray-700 dark:bg-coolGray-900 dark:text-coolGray-100 bg-background focus:ring-primary" 
                               onChange={handleChange}
                               value={values.email}
                               onBlur={handleBlur}
                               data-test="email"
                               />
                               {touched.email && errors.email ? (
                                <div className='text-red-500 text-sm'>{errors.email}</div>
                                ): null}
                               </div>
                               <div>
                                    <label htmlFor="phone" className="block mb-2 text-xs md:text-sm">Phone number</label>
                                    <input type="phone" 
                                    name="phone" 
                                    id="phone" 
                                    placeholder="9022288777" 
                                    className="w-full px-2 py-1 md:px-3 md:py-2 border rounded-md dark:border-coolGray-700 dark:bg-coolGray-900 dark:text-coolGray-100 bg-background focus:ring-primary"
                                    onChange={handleChange}
                                    value={values.phone}
                                    onBlur={handleBlur}
                                    data-test="phone"
                                    />
                                    {touched.phone && errors.phone ? (
                                <div className='text-red-500 text-sm'>{errors.phone}</div>
                                ): null}
                                </div>
                           </div>
                        ) :null}
                        {count === 2 ? (
                            <div>
                            <div>
                                <div className="flex justify-between mb-2">
                                    <label htmlFor="password" className="text-xs md:text-sm">Password</label>
                                    </div>
                                    <input type="password" 
                                    name="password" 
                                    id="password" 
                                    placeholder="*****" 
                                    className="w-full px-2 py-1 md:px-3 md:py-2 border rounded-md dark:border-coolGray-700 dark:bg-coolGray-900 dark:text-coolGray-100 bg-background focus:ring-primary" 
                                    onChange={handleChange}
                                    value={values.password}
                                    onBlur={handleBlur}
                                    data-test="password"
                                    />
                                    {touched.password && errors.password ? (
                                    <div className='text-red-500 text-sm'>{errors.password}</div>
                                    ): null}
                            </div>
                                 <div>
                                    <label htmlFor="password" className="text-xs md:text-sm mb-2 block">Industry</label>
                                        <Select options={optionsIndustry} 
                                            data-test="select-industry"
                                            onChange={selectedOption => {
                                                let event = { target : { name:'industry',value: selectedOption.value}}
                                                handleChange(event)
                                            }}
                                            value={values.industry.value}
                                            onBlur={handleBlur}
                                            theme={(theme) => ({
                                                ...theme,
                                                colors: {
                                                ...theme.colors,
                                                primary25: '#f4f5fc',
                                                primary75:'#f4f5fc',
                                                primary: '#1b174a',
                                                },
                                            })}
                                        />
                                </div>
                                <div>
                                    <label htmlFor="password" className="text-xs md:text-sm mb-2 block">Department</label>
                                        <Select options={optionsDepartment} 
                                            data-test="select-department"
                                            onChange={selectedOption => {
                                                let event = { target : { name:'department',value: selectedOption.value}}
                                                handleChange(event)
                                            }}
                                            value={values.department.value}
                                            onBlur={handleBlur}
                                            theme={(theme) => ({
                                                ...theme,
                                                colors: {
                                                ...theme.colors,
                                                primary25: '#f4f5fc',
                                                primary75:'#f4f5fc',
                                                primary: '#1b174a',
                                                },
                                            })}
                                        />
                                </div>
                            </div>

                        ) : null}
                       

                       
                        <div className='flex justify-between items-center'>
                             {count < 2 ?(
                                <button className='w-full px-5 py-2 md:px-8 md:py-3 rounded-md dark:bg-violet-400 dark:text-coolGray-900 bg-primary text-white flex items-center justify-center mb-6 mt-3'
                                 onClick={(e) => {
                                     e.preventDefault();
                                     setcount(count + 1)
                                 }}
                                 disabled={count > 1}>Countinue</button>
                            ) : null}
                        </div>
                    </div>
                    {count === 2 ? (
                        <div className="">
                            <div>
                                <div className='flex justify-end mb-2 items-center'>
                                    <svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor" className="w-3 mr-1">
                                    <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M10 19l-7-7m0 0l7-7m-7 7h18" />
                                    </svg>
                                    <button className='text-sm cursor-pointer'
                                    onClick={() => setcount(count - 1)}
                                    disabled={count < 2}
                                    >Previous Step</button>
                                </div>
                                <button type="button" className="w-full px-5 py-2 md:px-8 md:py-3 rounded-md dark:bg-violet-400 dark:text-coolGray-900 bg-primary text-white flex items-center justify-center"
                                 onClick={handleSubmit} 
                                 data-test="register-btn"
                                 disabled={!isValid}
                                 >
                                    {isLoading ? 
                                    <Loader
                                    type="ThreeDots"
                                    color="#ffff"
                                    height={20}
                                    width={20} //3 secs
                                    /> : <span> Sign up </span>
                                    }  
                                </button>
                            </div>
                            <div className='flex justify-center items-center'>
                            <p className="text-xs md:text-sm text-center dark:text-coolGray-400">Already have an account?</p>
                            <Link to="/login">
                                <span className='text-sm'>Sign in</span>
                            </Link>
                            </div>
                            <div className='flex items-center justify-center'>
                            <span className='text-sm text-red-500 text-center'>{error}</span>
                            </div>
                     </div>
                    ): null}
                </form>
            </div>           
        </div>
    )
}

// FIRSTNAME LASTNAME EMAIL PHONE NUMBER PASSWORD INDUSTRY DEPARTMENT

export default Register
